import React from "react";
import {Navbar,Nav,Container,NavDropdown} from 'react-bootstrap';
import { BrowserRouter as Router, Link } from "react-router-dom";

const NavBarmain = () => {
  return(
    <div>
      <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <div className="container-fluid">
    <Link to='/'>
    <img
          src="./caslog.jpg"
          width="60"
          height="60"
          className="d-inline-block align-top"
          alt="Real State Company"
        /> 
    </Link>
    <Link className="nav-link text-reset"  to='/'><h4>Real State Company</h4> </Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarNavDropdown">
      <ul className="navbar-nav mx-auto">
        
        <li className="nav-item">
          <Link className="nav-link "  to='/ListOwners'>List Owners</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to='/ListProperties'>List Properties</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to='/ListPropertiesIm'>List Properties Images</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to='/ListPropertiesTraces'>List Properties Traces</Link>
        </li>
      </ul>
    </div>
  </div>
</nav>
    </div>
  
  );
      
};

export default NavBarmain;