
import React, { useState , useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import {Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faEdit,faTrashAlt, faUsers } from '@fortawesome/free-solid-svg-icons';
import ApiUrlP from '../../Services/ApiRestP';

function ListProperties() {
  const [data, setData]=useState([]);
  const [modalEditar, setModalEditar]=useState(false);
  const [modalInsertar, setModalInsertar]=useState(false);
  const [modalEliminar, setModalEliminar]=useState(false);
  const [gestorSeleccionado, setGestorSeleccionado]=useState({
    p_IdProperty: '',
    p_Name: '',
    p_Addres: '',
    p_Price: '',
    p_CodeInternal: '',
    p_Year: '',
    p_IdOwner: ''
        
  })
  const handleChange=e=>{
    const {name, value}=e.target;
    setGestorSeleccionado({
      ...gestorSeleccionado,
      [name]: value
    });
    console.log(gestorSeleccionado);
  }

  const abrirCerrarModalInsertar=()=>{
    setModalInsertar(!modalInsertar);
  }

  const abrirCerrarModalEditar=()=>{
    setModalEditar(!modalEditar);
  }

   const abrirCerrarModalEliminar=()=>{
    setModalEliminar(!modalEliminar);
  }

  const peticionGet=async()=>{
     await axios.get(ApiUrlP)
     .then(response=>{
       setData(response.data);
     }).catch(error=>{
       console.log(error);
     })
   }
  const peticionPost=async()=>{
    delete gestorSeleccionado.p_IdProperty;
    gestorSeleccionado.p_Price=parseFloat(gestorSeleccionado.p_Price);
    gestorSeleccionado.p_Year=parseInt(gestorSeleccionado.p_Year);
    await axios.post(ApiUrlP, gestorSeleccionado)
    .then(response=>{
      setData(data.concat(response.data));
      abrirCerrarModalInsertar();
    }).catch(error=>{
      console.log(error);
    })
  }

  const peticionPut=async()=>{
    gestorSeleccionado.lanzamiento=parseInt(gestorSeleccionado.p_IdProperty);
    await axios.put(ApiUrlP +"/"+gestorSeleccionado.p_IdProperty, gestorSeleccionado)
    .then(response=>{
      var respuesta=response.data;
      var dataAuxiliar=data;
      dataAuxiliar.map(gestor=>{
        if(gestor.p_IdProperty===gestorSeleccionado.p_IdProperty){
          gestor.p_Name=respuesta.p_Name;
          gestor.p_Addres=respuesta.p_Addres;
          gestor.p_Price=respuesta.p_Price;
          gestor.p_CodeInternal=respuesta.p_CodeInternal;
          gestor.p_Year=respuesta.p_Year;
          gestor.p_IdOwner=respuesta.p_IdOwner;
        }
      });
      abrirCerrarModalEditar();
    }).catch(error=>{
      console.log(error);
    })
  }
  const peticionDelete=async()=>{
    console.log( gestorSeleccionado.p_IdProperty);
    await axios.delete(ApiUrlP+"/"+ gestorSeleccionado.p_IdProperty)
    .then(response=>{
     setData(data.filter(gestor=>gestor.p_IdProperty!==response.data));
     peticionGet();
      abrirCerrarModalEliminar();
    }).catch(error=>{
      console.log(error);
    })
  }
  const seleccionarGestor=(gestor, caso)=>{
    console.log(gestor);
    setGestorSeleccionado(gestor);
    (caso==="Editar")?
    abrirCerrarModalEditar(): abrirCerrarModalEliminar();
  }
  useEffect(()=>{
    peticionGet();
  },[])

  return (
    <div className="App">
       <div className='container'>
      <br/><br/>
      <button onClick={()=>abrirCerrarModalInsertar()} className="btn btn-primary"><FontAwesomeIcon icon={faUsers}/> New Properties</button>
      <br/><br/>
      <table className="table table-striped">
        <thead>
          <tr>
            <th>Id</th>
            <th>Name</th>
            <th>Addres</th>
            <th>Price</th>
            <th>CodeInternal</th>
            <th>Year</th>
            <th>IdOwner</th>
            <th>Accions</th>
          </tr>
        </thead>
        <tbody>

        {data.map((value,index)=>{
                  return (
                    <tr key={index}>
                    <td>{value.p_IdProperty}</td>
                    <td>{value.p_Name}</td>
                    <td>{value.p_Addres}</td>
                    <td>{"$ " + new Intl.NumberFormat("en-En").format(value.p_Price)}</td>
                    <td>{value.p_CodeInternal}</td>
                    <td>{value.p_Year}</td>
                    <td>{value.p_IdOwner}</td>
                    <td>
                    <button className="btn btn-primary" onClick={()=>seleccionarGestor(value, "Editar")}> <FontAwesomeIcon icon={faEdit}/> Edit</button> {"  "} {"  "}
                    <button className="btn btn-primary" onClick={()=>seleccionarGestor(value, "Eliminar")}><FontAwesomeIcon icon={faTrashAlt}/> Delete</button>
                  
                  
                  </td>
                    </tr>

                  )})}
        </tbody>

      </table>

      <Modal isOpen={modalInsertar}>
      <ModalHeader>Insert Properties</ModalHeader>
      <ModalBody>
        <div className="form-group">
          <label>Name: </label>
          <br />
          <input type="text" className="form-control" name="p_Name"  onChange={handleChange} />
          <label>Addres: </label>
          <br />
          <input type="text"  className="form-control" name="p_Addres"  onChange={handleChange}/>
          <br />
          <label>Price: </label>
          <br />
          <input type="text" className="form-control" name="p_Price" onChange={handleChange}/>
          <label>Code Internal: </label>
          <br />
          <input type="text" className="form-control" name="p_CodeInternal" onChange={handleChange}/>
          <label>Year: </label>
          <br />
          <input type="text" className="form-control" name="p_Year" onChange={handleChange}/>
          <label>Id Owner: </label>
          <br />
          <input type="text" className="form-control" name="p_IdOwner" onChange={handleChange}/>       
          <br />
        </div>
      </ModalBody>
      <ModalFooter>
        <button className="btn btn-primary" onClick={()=>peticionPost()}>Insertar</button>{"   "}
        <button className="btn btn-primary" onClick={()=>abrirCerrarModalInsertar()}>Cancelar</button>
      </ModalFooter>
    </Modal>

    <Modal isOpen={modalEditar}>
      <ModalHeader>Modify Property</ModalHeader>
      <ModalBody>
        <div className="form-group">
        <label>ID: </label>
          <br />
          <input type="text" className="form-control" name="p_ID" readOnly value={gestorSeleccionado && gestorSeleccionado.p_IdProperty}/>
          <br />
          <label>Name: </label>
          <br />
          <input type="text" className="form-control" name="p_Name"  onChange={handleChange} value={gestorSeleccionado && gestorSeleccionado.p_Name}/>
          <label>Addres: </label>
          <br />
          <input type="text"  className="form-control" name="p_Addres"  onChange={handleChange} value={gestorSeleccionado && gestorSeleccionado.p_Addres}/>
          <br />
          <label>Price: </label>
          <br />
          <input type="text" className="form-control" name="p_Price" onChange={handleChange} value={gestorSeleccionado && gestorSeleccionado.p_Price}/>
          <label>Code Internal: </label>
          <br />
          <input type="text" className="form-control" name="p_CodeInternal" onChange={handleChange} value={gestorSeleccionado && gestorSeleccionado.p_CodeInternal}/>
          <label>Year: </label>
          <br />
          <input type="text" className="form-control" name="p_Year" onChange={handleChange} value={gestorSeleccionado && gestorSeleccionado.p_Year}/>
          <label>Id Owner: </label>
          <br />
          <input type="text" className="form-control" name="p_IdOwner" onChange={handleChange} value={gestorSeleccionado && gestorSeleccionado.p_IdOwner}/>
          
        </div>
      </ModalBody>
      <ModalFooter>
        <button className="btn btn-primary" onClick={()=>peticionPut()}>Accept</button>{"   "}
        <button className="btn btn-primary" onClick={()=>abrirCerrarModalEditar()}>Cancel</button>
      </ModalFooter>
    </Modal>


    <Modal isOpen={modalEliminar}>
        <ModalBody>
        ¿are you sure of delete Property {gestorSeleccionado && gestorSeleccionado.p_Name } ?
        </ModalBody>
        <ModalFooter>
          <button className="btn btn-danger" onClick={()=>peticionDelete()}>
            Yes
          </button>
          <button
            className="btn btn-secondary"
            onClick={()=>abrirCerrarModalEliminar()}
          >
            No
          </button>
        </ModalFooter>
      </Modal>
      </div>
    </div>
  );
}
export default ListProperties;
