import React, { useState , useEffect} from 'react';
import axios from 'axios';
import ApiUrlPI from '../../Services/ApiRestPI';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faEdit,faTrashAlt, faUsers } from '@fortawesome/free-solid-svg-icons';
import {Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
const ListPropertiesIm = () => {
   const [propertyImg,setpropertyImgs] = useState([]);
   const [modalInsertar, setModalInsertar]=useState(false);
   const [modalEditar, setModalEditar]=useState(false);
   const [modalEliminar, setModalEliminar]=useState(false);
   const [propertyImgSeleccionado, setpropertyImgSeleccionado]=useState({
    pI_IdPropertyImage: '',
    pI_IdProperty: '',
    pI_File: '',
    pI_Enable: ''
  })
  const handleChange=e=>{
    const {names, value}=e.target;
    setpropertyImgSeleccionado({
      ...propertyImgSeleccionado,
      [names]: value
    });
    console.log(propertyImgSeleccionado);
  }

  const abrirCerrarModalInsertar=()=>{
    setModalInsertar(!modalInsertar);
  }

  const abrirCerrarModalEditar=()=>{
    setModalEditar(!modalEditar);
  }

   const abrirCerrarModalEliminar=()=>{
    setModalEliminar(!modalEliminar);
  }
  
   const getpropertyImgs = async() =>{
     const response = await axios.get(ApiUrlPI);
    //   console.log(response.data);
      setpropertyImgs(response.data);
   };
   
    const peticionPost=async()=>{
      propertyImgSeleccionado.pI_IdProperty=parseFloat(propertyImgSeleccionado.pI_IdProperty);
     await axios.post(ApiUrlPI, propertyImgSeleccionado)
     .then(response=>{
       setpropertyImgs(propertyImg.concat(response.data));
       abrirCerrarModalInsertar();
     }).catch(error=>{
       console.log(error);
     })
   }

const peticionDelete=async()=>{
    await axios.delete(ApiUrlPI+"/"+propertyImgSeleccionado.pI_IdPropertyImage)
    .then(response=>{
     setpropertyImgs(propertyImg.filter(gestor=>gestor.pI_IdPropertyImage!==response.pI_IdPropertyImage));
      abrirCerrarModalEliminar();
    }).catch(error=>{
      console.log(error);
    })
  }


  const peticionPut=async()=>{
    propertyImgSeleccionado.pI_IdPropertyImage=parseInt(propertyImgSeleccionado.pI_IdPropertyImage);
    await axios.put(ApiUrlPI+"/"+propertyImgSeleccionado.pI_IdPropertyImage, propertyImgSeleccionado)
    .then(response=>{
      var respuesta=response.data;
      var dataAuxiliar=propertyImg;
      dataAuxiliar.map(gestor=>{
        if(gestor.pI_IdPropertyImage===propertyImgSeleccionado.pI_IdPropertyImage){
          gestor.pI_IdProperty=respuesta.pI_IdProperty;
          gestor.pI_File=respuesta.pI_File;
          gestor.pI_Enable=respuesta.pI_Enable;
        }
      });
      abrirCerrarModalEditar();
    }).catch(error=>{
      console.log(error);
    })
  }

  const seleccionarpropertyImg=(gestor, caso)=>{
    console.log(gestor);
    setpropertyImgSeleccionado(gestor);
    (caso==="Editar")?
    abrirCerrarModalEditar(): abrirCerrarModalEliminar();
  }

   useEffect (() => {
    getpropertyImgs();
   },[])

    return (<div className="App"> 
        <div className='container'>
        <br/>
            <div className="card-body">
            <button onClick={()=>abrirCerrarModalInsertar()} className="btn btn-primary"><FontAwesomeIcon icon={faUsers}/> New Properties Images</button>
                <table className="table table-striped">
                <thead>
                    <tr>
                    <th scope="col">#Id Property Image</th>
                    <th scope="col">Id Property</th>
                    <th scope="col">File</th>
                    <th scope="col">Enable</th>
                    <th>Accions</th>
                    </tr>
                </thead>
                <tbody>
                
                {propertyImg.map((propertyImg,index)=>(
                  
                        <tr key={index}>
                        <td>{propertyImg.pI_IdPropertyImage}</td>
                        <td>{propertyImg.pI_IdProperty}</td>
                        <td>{propertyImg.pI_File}</td>
                        <td>{propertyImg.pI_Enable}</td>
                        <td>
                        <button className="btn btn-primary" onClick={()=>seleccionarpropertyImg(propertyImg, "Editar")}> <FontAwesomeIcon icon={faEdit}/> Edit</button> {"  "}
              <button className="btn btn-primary" onClick={()=>seleccionarpropertyImg(propertyImg, "Eliminar")}><FontAwesomeIcon icon={faTrashAlt}/> Delete</button>
			             </td>
                </tr>
                    ))
                }
               </tbody>
                </table>
                <Modal isOpen={modalInsertar}>
                <ModalHeader>New Properties Images</ModalHeader>
                <ModalBody>
                <div className="form-group">
                <label>Property: </label>
                <br />
                <input type="text" className="form-control" name="pI_IdProperty"  onChange={handleChange} />
                <br />
                <label>file: </label>
                <br />
                <input type="file" accept=".jpg, .png" name="pI_File" className="form-control" onChange={handleChange} />
                <br />
                <label>Enable: </label>
                <br />
                <input type="checkbox" className="form-check-input"  name='pI_Enable' onChange={handleChange}/>
                <br />
                </div>
                </ModalBody>
                <ModalFooter>
                  <button className="btn btn-primary" onClick={()=>peticionPost()}>Insertar</button>{"   "}
                  <button className="btn btn-primary" onClick={()=>abrirCerrarModalInsertar()}>Cancelar</button>
                  </ModalFooter>
                        </Modal>
                <Modal isOpen={modalEditar}>
                

              
                <ModalHeader>Edit Property Image</ModalHeader>
                <ModalBody>
                <div className="form-group">
                    <label>Id: </label>
                    <input type="text" className="form-control" name="pI_IdPropertyImage"  onChange={handleChange}  readOnly value={propertyImgSeleccionado && propertyImgSeleccionado.pI_IdPropertyImage}/>
                    <br />
                    <label>Property: </label>
                    <br />
                    <input type="text" className="form-control" name="pI_IdProperty"  onChange={handleChange}  value={propertyImgSeleccionado && propertyImgSeleccionado.pI_IdProperty}/>
                    <br />
                    <label>File: </label>
                    <br />
                    <input type="file" accept=".jpg, .png" name="pI_File" className="form-control" onChange={handleChange} value={propertyImgSeleccionado && propertyImgSeleccionado.pI_File}/>
                    <br />
                    <label>Enable: </label>
                    <br />
                    <input className="form-check-input" type="checkbox"  name="pI_Enable" value={propertyImgSeleccionado && propertyImgSeleccionado.pI_Enable}/>
                    <br />
                </div>
                </ModalBody>
                <ModalFooter>
                    <button className="btn btn-primary" onClick={()=>peticionPut()}>Editar</button>{"   "}
                    <button className="btn btn-primary" onClick={()=>abrirCerrarModalEditar()}>Cancelar</button>
                </ModalFooter>
                </Modal>


                <Modal isOpen={modalEliminar}>
                <ModalBody>
                ¿Are you sure delete this register ?
                </ModalBody>
                <ModalFooter>
                <button className="btn btn-primary" onClick={()=>peticionDelete()}>
                    Yes
                </button>
                <button
                    className="btn btn-danger"
                    onClick={()=>abrirCerrarModalEliminar()}
                >
                    No
                </button>
                </ModalFooter>
            </Modal>
            </div>
                </div>
              </div>
 );
};


export default ListPropertiesIm;