import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit, faTrashAlt, faUsers } from '@fortawesome/free-solid-svg-icons';
import axios from 'axios';
import { Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap';
import baseUrl from '../../Services/ApiRest';

function ListOwners() {
  //const baseUrl="https://localhost:44304/api/Owner";
  const [data, setData] = useState([]);
  const [modalEditar, setModalEditar] = useState(false);
  const [modalInsertar, setModalInsertar] = useState(false);
  const [modalEliminar, setModalEliminar] = useState(false);
  const [gestorSeleccionado, setGestorSeleccionado] = useState({
    id: '',
    name: '',
    photo: '',
    birthday: ''
  })

  const handleChange = e => {
    const { name, value } = e.target;
    setGestorSeleccionado({
      ...gestorSeleccionado,
      [name]: value
    });
    console.log(gestorSeleccionado);
  }

  const abrirCerrarModalInsertar = () => {
    setModalInsertar(!modalInsertar);
  }

  const abrirCerrarModalEditar = () => {
    setModalEditar(!modalEditar);
  }

  const abrirCerrarModalEliminar = () => {
    setModalEliminar(!modalEliminar);
  }

  const peticionGet = async () => {
    await axios.get(baseUrl)
      .then(response => {
        setData(response.data);
      }).catch(error => {
        console.log(error);
      })
  }

  const peticionPost = async () => {
    delete gestorSeleccionado.id;
    await axios.post(baseUrl, gestorSeleccionado)
      .then(response => {
        setData(data.concat(response.data));
        abrirCerrarModalInsertar();
        peticionGet();
      }).catch(error => {
        console.log(error);
      })
  }

  const peticionPut = async () => {
    gestorSeleccionado.lanzamiento = parseInt(gestorSeleccionado.id);
    await axios.put(baseUrl + "/" + gestorSeleccionado.id, gestorSeleccionado)
      .then(response => {
        var respuesta = response.data;
        var dataAuxiliar = data;
        dataAuxiliar.map(gestor => {
          if (gestor.id === gestorSeleccionado.id) {
            gestor.name = respuesta.name;
            gestor.photo = respuesta.photo;
            gestor.birthday = respuesta.birthday;
          }
        });
        abrirCerrarModalEditar();
      }).catch(error => {
        console.log(error);
      })
  }

  const peticionDelete = async () => {
    await axios.delete(baseUrl + "/" + gestorSeleccionado.id)
      .then(response => {
        setData(data.filter(gestor => gestor.id !== response.data));
        abrirCerrarModalEliminar();
      }).catch(error => {
        console.log(error);
      })
  }

  const seleccionarOwner = (gestor, caso) => {

    console.log(gestor);
    setGestorSeleccionado(gestor);
    (caso === "Editar") ?
      abrirCerrarModalEditar() : abrirCerrarModalEliminar();
  }

  useEffect(() => {
    peticionGet();
  }, [])

  return (
    <div className="App">
      <div className='container'>
        <br /><br />
        <button onClick={() => abrirCerrarModalInsertar()} className="btn btn-primary"><FontAwesomeIcon icon={faUsers} /> New Owners</button>
        <br /><br />
        <table className="table table-striped">
          <thead>
            <tr>
              <th>Id</th>
              <th>Name</th>
              <th>Photo</th>
              <th>Birthday</th>
              <th>Accions</th>
            </tr>
          </thead>
          <tbody>

            {data.map((value, index) => {
              return (
                <tr key={index}>
                  <td>{value.id}</td>
                  <td>{value.name}</td>
                  {/* <td>{value.photo}</td>  */}
                  <td>
                    <img src={`data:image/jpeg;base64,${value.photo}`} height="40" width="120" /></td>
                  {/* <input type="image" src={{uri: `data:image/jpeg;base64,{0},${value.photo}.jpg`}} alt="photo" height="150" width="120" /></td> */}
                  {/* <td> source={{uri: `data:image/jpg;base64,${value.photo}`}}</td> */}

                  <td>{value.birthday}</td>
                  <td>
                    <button className="btn btn-primary" onClick={() => seleccionarOwner(value, "Editar")}> <FontAwesomeIcon icon={faEdit} /> Edit</button> {"  "}
                    <button className="btn btn-primary" onClick={() => seleccionarOwner(value, "Eliminar")}><FontAwesomeIcon icon={faTrashAlt} /> Delete</button>
                  </td>
                </tr>
              )
            })}
          </tbody>
        </table>
        <Modal isOpen={modalInsertar}>
          <ModalHeader>Insert Owner</ModalHeader>
          <ModalBody>
            <div className="form-group">
              <label>Name: </label>
              <br />
              <input type="text" className="form-control" name="name" onChange={handleChange} />
              <label>Photo: </label>
              <br />
              <input type="file" accept=".jpg, .png" className="form-control" name="photo" onChange={handleChange} />
              <br />
              <label>Date of Birthday: </label>
              <input type="date" name="birthday" onChange={handleChange} className="form-control" placeholder="dd-mm-yyyy" />
            </div>
          </ModalBody>
          <ModalFooter>
            <button className="btn btn-primary" onClick={() => peticionPost()}>Insertar</button>{"   "}
            <button className="btn btn-primary" onClick={() => abrirCerrarModalInsertar()}>Cancelar</button>
          </ModalFooter>
        </Modal>

        <Modal isOpen={modalEditar}>
          <ModalHeader>Modify Owner</ModalHeader>
          <ModalBody>
            <div className="form-group">
              <label>ID: </label>
              <br />
              <input type="text" className="form-control" name="id" readOnly value={gestorSeleccionado && gestorSeleccionado.id} />
              <br />
              <label>Name: </label>
              <br />
              <input type="text" className="form-control" name="name" onChange={handleChange} value={gestorSeleccionado && gestorSeleccionado.name} autoComplete="off" />
              <br />
              <label>Photo: </label>
              <br />
              <input type="file" accept=".jpg, .png" className="form-control" name="photo" /><img src={`data:image/jpeg;base64,${gestorSeleccionado.photo}`} height="50" width="50" value={gestorSeleccionado && gestorSeleccionado.photo} autoComplete="off" />
              <br />
              <label>Date of Birthday: </label>
              <br />
              <input type="date" name="birthday" onChange={handleChange} className="form-control" value={gestorSeleccionado && gestorSeleccionado.birthday} />
              <br />
            </div>
          </ModalBody>
          <ModalFooter>
            <button className="btn btn-primary" onClick={() => peticionPut()}>Accept</button>{"   "}
            <button className="btn btn-primary" onClick={() => abrirCerrarModalEditar()}>Cancel</button>
          </ModalFooter>
        </Modal>
        <Modal isOpen={modalEliminar}>
          <ModalBody>
            ¿Estás seguro que deseas eliminar el Gestor de Base de datos {gestorSeleccionado && gestorSeleccionado.name}?
          </ModalBody>
          <ModalFooter>
            <button className="btn btn-primary" onClick={() => peticionDelete()}>
              Sí
            </button>
            <button
              className="btn btn-secondary"
              onClick={() => abrirCerrarModalEliminar()}
            >
              No
            </button>
          </ModalFooter>
        </Modal>
      </div>
    </div>
  );
}
export default ListOwners;
