import React, { useState , useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import {Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faEdit,faTrashAlt, faUsers } from '@fortawesome/free-solid-svg-icons';
import ApiUrlTr from '../../Services/ApiRestTr';

function ListPropertiesTraces() {
  const [propertytrace, setData]=useState([]);
  const [modalEditar, setModalEditar]=useState(false);
  const [modalInsertar, setModalInsertar]=useState(false);
  const [modalEliminar, setModalEliminar]=useState(false);
  const [propertytraceSeleccionado, setpropertytraceSeleccionado]=useState({
    p_IdProperty: '',
    p_DateSale:'',
    p_Value:'',
    p_Tax : '',
    p_Year :'',
    p_IdPropertyTrace :'',
    p_Name: ''
        
  })
  const handleChange=e=>{
    const {name, value}=e.target;
    setpropertytraceSeleccionado({
      ...propertytraceSeleccionado,
      [name]: value
    });
    console.log(propertytraceSeleccionado);
  }

  const abrirCerrarModalInsertar=()=>{
    setModalInsertar(!modalInsertar);
  }

  const abrirCerrarModalEditar=()=>{
    setModalEditar(!modalEditar);
  }

   const abrirCerrarModalEliminar=()=>{
    setModalEliminar(!modalEliminar);
  }

  const peticionGet=async()=>{
     await axios.get(ApiUrlTr)
     .then(response=>{
       setData(response.data);
     }).catch(error=>{
       console.log(error);
     })
   }
  const peticionPost=async()=>{
    delete propertytraceSeleccionado.p_IdPropertyTrace;
    propertytraceSeleccionado.p_Price=parseFloat(propertytraceSeleccionado.p_Price);
    propertytraceSeleccionado.p_Year=parseInt(propertytraceSeleccionado.p_Year);
    await axios.post(ApiUrlTr, propertytraceSeleccionado)
    .then(response=>{
      setData(propertytrace.concat(response.data));
      abrirCerrarModalInsertar();
    }).catch(error=>{
      console.log(error);
    })
  }

  const peticionPut=async()=>{
    propertytraceSeleccionado.lanzamiento=parseInt(propertytraceSeleccionado.p_IdPropertyTrace);
    await axios.put(ApiUrlTr +"/"+propertytraceSeleccionado.p_IdPropertyTrace, propertytraceSeleccionado)
    .then(response=>{
      var respuesta=response.data;
      var dataAuxiliar=propertytrace;
      dataAuxiliar.map(gestor=>{
        if(gestor.p_IdPropertyTrace===propertytraceSeleccionado.p_IdPropertyTrace){
         
          gestor.p_IdPropertyTraceTrace=respuesta.p_IdPropertyTraceTrace;
          gestor.p_DateSale=respuesta.p_DateSale;
          gestor.p_Value=respuesta.p_Value;
          gestor.p_Tax=respuesta.p_Tax;
          gestor.p_Year=respuesta.p_Year;
          gestor.p_IdPropertyTrace=respuesta.p_IdPropertyTrace;
          gestor.p_Name=respuesta.p_Name;
        }
      });
      abrirCerrarModalEditar();
    }).catch(error=>{
      console.log(error);
    })
  }
  const peticionDelete=async()=>{
    console.log( propertytraceSeleccionado.p_IdPropertyTrace);
    await axios.delete(ApiUrlTr+"/"+ propertytraceSeleccionado.p_IdPropertyTrace)
    .then(response=>{
     setData(propertytrace.filter(gestor=>gestor.p_IdPropertyTrace!==response.data));
     peticionGet();
      abrirCerrarModalEliminar();
    }).catch(error=>{
      console.log(error);
    })
  }
  const seleccionarpropertytrace=(gestor, caso)=>{
    console.log(gestor);
    setpropertytraceSeleccionado(gestor);
    (caso==="Editar")?
    abrirCerrarModalEditar(): abrirCerrarModalEliminar();
  }
  useEffect(()=>{
    peticionGet();
  },[])

  return (
    <div className="App">
       <div className='container'>
      <br/><br/>
      <button onClick={()=>abrirCerrarModalInsertar()} className="btn btn-primary"><FontAwesomeIcon icon={faUsers}/> New Properties</button>
      <br/><br/>
      <table className="table table-striped">
                <thead>
                    <tr>
                    <th scope="col">#Id Property Trace</th>
                    <th scope="col">DateSale</th>
                    <th scope="col">Value</th>
                    <th scope="col">Tax</th>
                    <th scope="col">Year</th>
                    <th scope="col">IdProperty</th>
                    <th scope="col">Name</th>
                    <th>Accions</th>
                    </tr>
                </thead>
                <tbody>
                {propertytrace.map((propertytrace,index)=>(
                        <tr key={index}>
                        <td>{propertytrace.p_IdPropertyTrace}</td>
                        <td>{propertytrace.p_DateSale}</td>
                        <td>{propertytrace.p_Value}</td>
                        <td>{propertytrace.p_Tax}</td>
                        <td>{propertytrace.p_Year}</td>
                        <td>{propertytrace.p_IdProperty}</td>
                        <td>{propertytrace.p_Name}</td>
                        <td>
                        <button className="btn btn-primary" onClick={()=>seleccionarpropertytrace(propertytrace, "Editar")}> <FontAwesomeIcon icon={faEdit}/> Edit</button> {"  "}
                       <button className="btn btn-primary" onClick={()=>seleccionarpropertytrace(propertytrace, "Eliminar")}><FontAwesomeIcon icon={faTrashAlt}/> Delete</button>
			             </td>
                </tr>
                    ))
                }
               </tbody>
                </table>
              

                <Modal isOpen={modalInsertar}>
                <ModalHeader> New Properties Traces</ModalHeader>
                <ModalBody>
                <div className="form-group">
                <label>DateSale: </label>
                <br />
                <input type="date" name="p_DateSale" onChange={handleChange} className="form-control" placeholder="dd-mm-yyyy" /> 
                <br />
                <label>Value: </label>
                <br />
                <input type="text" className="form-control" name="p_Value"  onChange={handleChange} />
                 <br />
                <label>Tax: </label>
                <br />
                <input type="text" className="form-control" name="p_Tax"  onChange={handleChange} />
                <br />
                <label>Year: </label>
                <br />
                <input type="text" className="form-control" name="p_Year"  onChange={handleChange} />
                <br />
                <label>IdProperty: </label>
                <br />
                <input type="text" className="form-control" name="p_IdProperty"  onChange={handleChange} />
                <br />
                <label>Name: </label>
                <br />
                <input type="text" className="form-control" name="p_Name"  onChange={handleChange} />
                </div>
                </ModalBody>
                <ModalFooter>
                <button className="btn btn-primary" onClick={()=>peticionPost()}>Insertar</button>{"   "}
                <button className="btn btn-primary" onClick={()=>abrirCerrarModalInsertar()}>Cancelar</button>
            </ModalFooter>
                </Modal>
                <Modal isOpen={modalEditar}>
                <ModalHeader>Update Properties Traces</ModalHeader>
                <ModalBody>
                 <div className="form-group">
                 <label>Id: </label>
                 <input type="text" className="form-control" name="p_IdPropertyTraceTrace"  onChange={handleChange} readOnly value={propertytraceSeleccionado && propertytraceSeleccionado.p_IdPropertyTrace}/> 
                 <br />
                <div className="form-group">
                <label>DateSale: </label>
                <br />
                <input type="date" name="p_DateSale" onChange={handleChange} className="form-control" value={propertytraceSeleccionado && propertytraceSeleccionado.p_DateSale}/> 
                <br />
                 <label>Value: </label>
                <br />
                <input type="text" className="form-control" name="p_Value"  onChange={handleChange} value={propertytraceSeleccionado && propertytraceSeleccionado.p_Value}/> 
                <br />
                <label>Tax: </label>
                <br />
                <input type="text" className="form-control" name="p_Tax"  onChange={handleChange} value={propertytraceSeleccionado && propertytraceSeleccionado.p_Tax}/> 
                <br />
                <label>Year: </label>
                <br />
                <input type="text" className="form-control" name="p_Year"  onChange={handleChange} value={propertytraceSeleccionado && propertytraceSeleccionado.p_Year}/> 
                <br />
                <label>IdProperty: </label>
                <br />
                <input type="text" className="form-control" name="p_IdProperty"  onChange={handleChange} value={propertytraceSeleccionado && propertytraceSeleccionado.p_IdProperty}/> 
                <br />
                <label>Name: </label>
                <br />
                <input type="text" className="form-control" name="p_Name"  onChange={handleChange} value={propertytraceSeleccionado && propertytraceSeleccionado.p_Name}/> 
                </div>
                </div>
                </ModalBody>
                <ModalFooter>
                    <button className="btn btn-primary" onClick={()=>peticionPut()}>Editar</button>{"   "}
                    <button className="btn btn-primary" onClick={()=>abrirCerrarModalEditar()}>Cancelar</button>
                </ModalFooter>
                </Modal>


                <Modal isOpen={modalEliminar}>
                <ModalBody>
                ¿Are you sure delete this register ?
                </ModalBody>
                <ModalFooter>
                <button className="btn btn-primary" onClick={()=>peticionDelete()}>
                    Yes
                </button>
                <button
                    className="btn btn-primary"
                    onClick={()=>abrirCerrarModalEliminar()}
                >
                    No
                </button>
                </ModalFooter>
            </Modal>
            </div>
          </div>
  );
}
export default ListPropertiesTraces;
