
import './Assets/css/App.css';
 import CarrouselHouses from './Carrousel/CarrouselHouses';
import NavBarmain from './Components/Layout/NavBarMain';
import {BrowserRouter as Router,Switch,Route} from 'react-router-dom';

  import ListOwners from './Components/Owners/ListOwners';
  import ListProperties from './Components/Properties/ListProperties';
  import ListPropertiesIm from './Components/PropertiesImages/ListPropertiesIm';
  import ListPropertiesTraces from './Components/PropertiesTraces/ListPropertiesTraces';
  import Footer from './Components/Footer/Footer';
function App() {
  return (
    <div className="container-fluid">
     <Router>
          <NavBarmain/>  
          <Switch>
          <Route path='/' exact component={CarrouselHouses}></Route>
            <Route path='/ListOwners' component={ListOwners}></Route>
            <Route path='/ListProperties' component={ListProperties}></Route>
            <Route path='/ListPropertiesIm' component={ListPropertiesIm}></Route>
            <Route path='/ListPropertiesTraces' component={ListPropertiesTraces}></Route>
          </Switch>
          <Footer></Footer>  
     </Router>
    </div>
  );
}

export default App;
